<template>
  <div class="home">
    <div class="tips">Prism</div>
    <el-table :data="tableData" style="width: 100%" class="tableBox">
      <el-table-column type="expand">
        <template #default="props">
          <div class="info">
            <h3>request_headers</h3>
            <p class="infoItem" v-for="(value,key) in props.row.request_headers" :key="key" :title="value"><span class="title">{{key}}: </span> {{ value }}</p>
            <h3>request_parma</h3>
            <pre class="paramInfo">{{props.row.request_parma}}</pre>
            <h3>request_body</h3>
            <pre class="headerInfo">{{props.row.request_body}}</pre>
            <h3>response_body</h3>
            <pre class="responseInfo">{{props.row.response_body}}</pre>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="请求方式">
        <template #default="props">
          <span v-if="props.row.request_method=='GET'" class="type getStyle">{{ props.row.request_method }}</span>
          <span v-else-if="props.row.request_method=='POST'" class="type postStyle">{{ props.row.request_method }}</span>
          <span v-else-if="props.row.request_method=='PUT'" class="type putStyle">{{ props.row.request_method }}</span>
          <span v-else class="type deleteStyle">{{ props.row.request_method }}</span>
        </template>
      </el-table-column>
      <el-table-column label="请求IP" prop="request_url">
        <template #default="props">{{props.row.request_dst_ip}}:{{props.row.request_dst_port}}</template>
      </el-table-column>
      <el-table-column label="接口名" prop="request_url" />
    </el-table>
    <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :total="total">
    </el-pagination>
    <div class="footer">
      Powered By zcw,wdd &nbsp;<a href="https://github.com/Zhouchaowen/prism" target="_blank">Github</a>
    </div>
  </div>
</template>

<script>
import { ref,onMounted } from 'vue';
import { getList } from "@/utils/modules/user.js";

export default {
  name: 'Home',
  setup(){
    const tableData = ref([])
    const currentIndex = ref(1)
    const limit = ref(10)
    const total = ref(0)

    onMounted(() => {
      getApiList()
    })

    const getApiList = () => {
      getList(currentIndex.value,limit.value).then(res => {
        if(res.data&&res.data.length>0) {
          total.value = res.total;
          tableData.value = res.data.map((item => {
            try {
              item['request_body'] = JSON.parse(item.request_body)
              // eslint-disable-next-line no-empty
            } catch (e) {
            }
            try {
              item['response_body'] = JSON.parse(item.response_body)
              // eslint-disable-next-line no-empty
            } catch (e) {
            }
            return item
          }))
        }
      })
    }
    const handleCurrentChange = (val) => {
      currentIndex.value = val
      getApiList()
    }
    return {
      tableData,
      total,
      handleCurrentChange
    }
  }
}
</script>

